.userPost {
	display:flex;
	justify-content: center;
	align-items: center;
	margin: 0;
	padding: 1.2rem 2.2rem;
}

.userInfo {
	flex-basis: 15%;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	margin: 0;
	padding: 0 .3rem;
}

.userPic {
	height: 50px;
	width: 50px;
	border: 0;
	border-radius: 50%;
}

.date,
.userName {
	width: 100%;
	text-align: center;
}

.postContent {
	flex-basis: 85%;
	position: relative;
	margin: 0;
	padding: 0 .5rem;
}

.postText {
	
}

.controls {
	margin: 0;
	padding: 0;
	max-width: 150px;
	max-height: 50px
}

.button {
	margin: 0;
	padding: .2rem .5rem;
}